<template>
    <el-dialog :title="formTitle" :value="value" v-model="visible" @close="resetFormData">
        <el-form :model="formData" label-width="120px" :rules="formRules" ref="formData">
            <el-form-item label="商品名称" prop="name" required>
                <el-input v-model.trim="formData.name" auto-complete="off"></el-input>
            </el-form-item>

            <el-form-item required label="商品类型" prop="type">
                <el-select v-model="formData.type" placeholder="请选择类型">
                    <el-option label="商品型" value="0"></el-option>
                    <el-option label="壁纸型" value="1"></el-option>
                </el-select>
            </el-form-item>


            <el-form-item required label="商品价格" prop="price">
                <el-input v-model="formData.price" placeholder="单位为:元"></el-input>
            </el-form-item>

            <el-form-item label="快递费用" prop="expressPrice">
                <el-input v-model="formData.expressPrice"></el-input>
            </el-form-item>

            <el-form-item required label="绑定商家" prop="authorId">
                <template>
                    <el-select style="width: 50%;" v-model="formData.authorId" placeholder="输入关键词搜索作者" filterable>
                        <el-option v-for="item in authorList" :key="item.id" :label="item.name"
                                   :value="item.id"></el-option>
                    </el-select>
                </template>
            </el-form-item>


            <el-form-item label="样式" prop="styleNames" v-if="formData.type==0">
                <el-tag style="margin-right: 10px;" :key="word" v-for="word in formData.styleNames" :closable="true"
                        :close-transition="false"
                        @close="delStyle(word)">
                    {{word}}
                </el-tag>
                <el-input style="width: 100px;" class="input-new-tag" v-if="inputVisible" v-model="inputValue"
                          ref="saveTagInput"
                          @keyup.enter.native="confirmStyle" @blur="confirmStyle">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="addStyle">添加</el-button>
            </el-form-item>

            <el-form-item label="尺寸" prop="sizeNames">
                <el-tag style="margin-right: 10px;" :key="word" v-for="word in formData.sizeNames" :closable="true"
                        :close-transition="false"
                        @close="delSize(word)">
                    {{word}}
                </el-tag>
                <el-input style="width: 100px;" class="input-new-tag" v-if="inputVisible2" v-model="inputValue2"
                          ref="saveTagInput2"
                          @keyup.enter.native="confirmSize" @blur="confirmSize">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="addSize">添加</el-button>
            </el-form-item>


            <el-form-item :label="formData.type ==='0' ? '商品封面': '壁纸封面'" required>
                <el-upload
                    class="avatar-uploader"
                    action="/ks3/image_upload"
                    :show-file-list="false"
                    :on-success="coverSuccess">
                    <img v-if="formData.coverUrl" :src="formData.coverUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>

            <el-form-item label="商品轮播图" required v-if="formData.type==0">
                <el-upload
                    action="/ks3/image_upload"
                    :file-list="formData.lunBoReadList"
                    :on-success="lunBoSuccess"
                    list-type="picture-card"
                    :on-preview="lunBoPrev"
                    :on-remove="lunBoRemove">
                    <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog :visible.sync="dialogVisible" :modal-append-to-body="false" :modal="false">
                    <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>
            </el-form-item>


            <!--壁纸轮播  图片 - 尺寸 组合   轮播-->
            <el-form-item label="壁纸" v-if="formData.type==1">
                <el-upload
                    class="upload-demo"
                    action="/ks3/image_upload"
                    :on-preview="stickSizePrev"
                    :on-remove="stickRemove"
                    :on-success="stickSizeSuccess"
                    :file-list="formData.stickReadList"
                    list-type="picture">
                    <el-button size="small" type="primary">点击上传</el-button>
                    <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                </el-upload>
            </el-form-item>

            <!--商品介绍-->
            <el-form-item label="商品介绍" prop='description'>
                <div v-for="(item,index) in formData.description">
                    <div class="mb-10">

                        <span class="mr-10" width="500" style="display: inline-block;">
                            <span
                                style="font-size: 20px"><strong>{{item.type==0?"文字":item.type==1?"标题":"图片"}} :</strong></span>

                             <span v-if="item.type < 2">{{item.content}}</span>
                             <span v-else>
                             <img v-if="item.fullUrl != ''" style="width: 100%;vertical-align:middle;margin:10px auto;"
                                  :src="item.fullUrl" alt=""/>
                             </span>
                            </span>

                        <div class="btns" style="display: inline-block;">
                            <el-button size="small" @click.native="edit(item,index)" type="success">编辑</el-button>
                            <el-button size="small" @click.native="del(item)" type="danger">删除</el-button>
                        </div>

                    </div>
                </div>

                <el-select v-model="descType" placeholder="请选择">
                    <el-option label="文字" value="0"></el-option>
                    <el-option label="标题" value="1"></el-option>
                    <el-option label="图片" value="2"></el-option>
                </el-select>
                <el-button size="small" @click.native="sure(descType)">确定</el-button>

            </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click.native="change">取消</el-button>
            <el-button size="small" type="primary" @click.native="formSubmit" :loading="formLoading">提交</el-button>
        </div>

        <!--商品介绍弹框 -->
        <el-dialog
            width="50%"
            :title="商品操作"
            :value="productDilogData"
            :visible.sync="productVisible"
            :modal-append-to-body="false"
            top="70%"
            :modal="false">
            <el-form :model="productDilogData" ref="productDilogData">

                <el-form-item prop="content">
                    <el-input v-model="productDilogData.content" auto-complete="off" placeholder="请输入文字" type="textarea"
                              autosize="true"
                              v-if="productDilogData.type==0"></el-input>

                    <el-input v-model="productDilogData.content" auto-complete="off" placeholder="请输入段落标题"
                              type="textarea" autosize="true"
                              v-else-if="productDilogData.type==1"></el-input>

                    <el-upload v-else
                               class="avatar-uploader"
                               action="/ks3/image_upload"
                               :show-file-list="false"
                               :on-success="proSuccess">
                        <img v-if="productDilogData.content" :src="productImageUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click.native="cancel">取消</el-button>
                <el-button size="small" type="primary" @click.native="proSubmit">确定</el-button>
            </div>
        </el-dialog>


        <!--壁纸尺寸列表弹框-->
        <el-dialog :visible.sync="stickVisible" :modal-append-to-body="false" :modal="false" size="large" title="尺寸选择"
                   style="top:500px;">
            <el-form :model="formData" label-width="120px" ref="formData">

                <el-form-item label="尺寸">
                    <el-select placeholder="请选择尺寸" v-model="sizeName">
                        <el-option :label="item" :value="item" v-for="item in formData.sizeNames"></el-option>
                    </el-select>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="stickVisible=false">取消</el-button>
                <el-button size="small" type="primary" @click.native="stickSizeSubmit">确定</el-button>
            </div>
        </el-dialog>
    </el-dialog>

</template>

<script type="es6">

    import {httpGet, httpPost} from "../../../api/api";
    import util from "../../../api/util";

    export default {
        name: 'vDetail',
        props: ['value', 'data', 'authors'],
        data() {
            let check = (rule, value, callback) => {
                if (this.formData.authorId === '') {
                    callback(new Error('请绑定作者'));
                }
                callback();
            };

            let numCheck = (rule, value, callback) => {
                let reg = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
                if (!reg.test(value)) {
                    callback(new Error('价格只能输入数字且保留两位小数字'));
                }
                callback();
            };

            let expressPriceCheck = (rule, value, callback) => {
                if (value == 0 || value === '') {
                    callback();
                }
                let reg = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
                if (!reg.test(value)) {
                    callback(new Error('价格是能输入数字且保留两位小数字'));
                }
                callback();
            };
            return {
                productVisible: false,
                visible: false, //默认隐藏
                stickVisible: false,
                showLoading: false,
                formTitle: '新增商品',
                formLoading: false,
                dialogImageUrl: '',
                dialogVisible: false,
                formRules: {
                    name: [
                        {required: true, message: '请输入商品名称', trigger: 'blur'},
                        {min: 1, max: 100, message: '长度在 1 到 100 个字符', trigger: 'blur'}
                    ],
                    authorId: [
                        {validator: check, trigger: 'blur'}
                    ],
                    price: [
                        {validator: numCheck, trigger: 'blur'}
                    ],
                    expressPrice: [
                        {validator: expressPriceCheck, trigger: 'blur'}
                    ],
                },
                formData: {
                    id: '',
                    name: '',
                    coverUrl: '',
                    coverId: '',
                    authorId: '',
                    type: "0",
                    price: 0.00,
                    expressPrice: 0.00,
                    styleNames: [],//样式
                    sizeNames: [],//尺寸
                    banners: [],//商品-轮播-写
                    picDescs: [],//壁纸-轮播-写 -> 存的是file { name, url, stickSize:{imageId,sizeName,url}}
                    lunBoReadList: [],//商品-轮播-读
                    stickReadList: [],//壁纸-轮播-读
                    description: []//商品介绍 格式：「type,content,fullUrl」
                },
                productDilogData: {
                    type: '',
                    content: '',
                    fullUrl: '',
                },
                descType: '0',//商品介绍类型
                index: '',
                sizeName: '',//当前-尺寸
                stickSizeId: '',//当前-stickSize图片
                inputVisible: false,
                inputVisible2: false,
                inputValue: '',
                inputValue2: '',
                authorList: [],
                textList: [],
                titleList: [],
            }
        },
        computed: {
            detail() { //返回详情
                let _self = this;
                _self.authorList = _self.authors;
                if (!_self.value) {
                    return;
                }
                if (_self.data.id && _self.visible) {
                    _self.formTitle = '编辑商品';
                    //查询
                    httpGet('productDetail', {id: _self.data.id}, _self, function (res) {
                        _self.tableLoading = false;
                        try {
                            let {error, status, data} = res;
                            _self.formData = data;
                            _self.formData.type = data.type + '';
                            //商品轮播banner 赋值
                            if (_self.formData.banners) {
                                _self.formData.lunBoReadList = _self.formData.banners;
                                let ids = _self.formData.banners.map(o => o.bannerId);
                                _self.formData.banners = ids;
                            } else {
                                _self.formData.banners = [];
                            }

                            //壁纸轮播赋值
                            //返回  sizeName; url imageId，和页面字段全部一样。picDescs 存的对象叫做 file 里面需要丢一份
                            if (_self.formData.picDescs) {
                                let stickLunbo = _self.formData.picDescs.map(o => {
                                    let obj = new Object();
                                    obj.stickSize = o;
                                    obj.name = o.sizeName;
                                    obj.url = o.url;
                                    return obj;
                                });
                                _self.formData.picDescs = stickLunbo;
                                _self.formData.stickReadList = stickLunbo;
                            } else {
                                _self.formData.picDescs = [];
                            }

                            //尺寸 对象转换
                            let sizeList = _self.formData.sizeNames;
                            if (sizeList) {
                                _self.formData.sizeNames = sizeList.map(o => o.name);
                            }
                            //样式对象转换
                            let styleList = _self.formData.styleNames;
                            if (styleList) {
                                _self.formData.styleNames = styleList.map(o => o.name);
                            }
                            //null 赋值为【】
                            if (!_self.formData.description) {
                                _self.formData.description = [];
                            } else {
                                _self.formData.description = _self.formData.description.map(o => {
                                    o.fullUrl = o.content
                                    return o;
                                })
                            }
                            console.log(_self.formData);
                        } catch (error) {
                            util.jsErrNotify(error);
                        }
                    });
                }
            }
        },
        methods: {
            formSubmit() { //提交表单
                let _self = this;
                let coverId = _self.formData.coverId;
                if (!coverId) {
                    _self.$message.error('请上传封面');
                    return;
                }
                if (_self.formData.type === '0') {
                    if (_self.formData.banners.length == 0) {
                        _self.$message.error('请上传商品轮播图');
                        return;
                    }
                }

                if (_self.formData.type === '1') {
                    if (_self.formData.picDescs.length == 0) {
                        _self.$message.error('请上传壁纸');
                        return;
                    }
                    if (_self.formData.sizeNames.length == 0) {
                        _self.$message.error('贴纸尺寸为必填项');
                        return;
                    }

                    //尺寸和壁纸 一对一校验  --  尺寸数 > 图片数量 数量不对等的情况
                    if (_self.formData.sizeNames.length != _self.formData.picDescs.length) {
                        _self.$message.error('一张壁纸需要对应一个尺寸');
                        return;
                    }
                }
                let expressPrice = _self.formData.expressPrice;
                if (expressPrice === '') {
                    expressPrice = 0;
                }
                let para = new Object();
                para.id = _self.formData.id;
                para.name = _self.formData.name;
                para.type = Number(_self.formData.type);
                para.coverId = _self.formData.coverId;
                para.authorId = _self.formData.authorId;
                para.price = _self.formData.price;
                para.expressPrice = expressPrice;
                para.sizeNames = _self.formData.sizeNames;
                para.styleNames = _self.formData.styleNames;

                //商品介绍
                para.description = _self.formData.description.map(o=>{
                    o.content=o.fullUrl;
                    return o;
                });
                //商品轮播图ID
                if (_self.formData.type === '0') {
                    let binnerId = _self.formData.banners.join(',');
                    para.bannerIds = binnerId;
                }

                //壁纸轮播图 和尺寸 一对一校验 -- 不选尺寸的情况
                if (_self.formData.type === '1') {
                    let picDesc = _self.formData.picDescs.map(o => {
                        let obj = o.stickSize;
                        if (obj.sizeName === '') {
                            _self.$message.error('请选择壁纸轮播图尺寸');
                            return;
                        }
                        return obj;
                    });
                    para.picDescs = picDesc;
                }
                _self.$refs.formData.validate((valid) => {
                    if (valid) {
                        _self.formLoading = true;
                        //新增
                        httpPost('productSave', para, _self, function (res) {
                            _self.formLoading = false;
                            try {
                                _self.$message.success('提交成功');
                                _self.resetFormData();
                                _self.$emit('refresh');
                                _self.visible = false;
                            } catch (error) {
                                util.jsErrNotify(error);
                            }
                        });
                    }
                });
            },
            change() {
                this.visible = false;
            },
            resetFormData() { //关闭表格弹窗，重置表格数据
                let _self = this;
                _self.formTitle = '新增产品';
                _self.formData = {
                    id: '',
                    name: '',
                    coverUrl: '',
                    coverId: '',
                    authorId: '',
                    type: "0",
                    price: 0.00,
                    expressPrice: 0.00,
                    styleNames: [],//样式
                    sizeNames: [],//尺寸
                    banners: [],//商品-轮播-写
                    picDescs: [],//壁纸-轮播-写 -> 存的是file { name, url, stickSize:{imageId,sizeName,url}}
                    lunBoReadList: [],//商品-轮播-读
                    stickReadList: [],//壁纸-轮播-读
                    description: []//商品介绍 格式：「type,content」
                };
            },
            confirmStyle() {
                let inputValue = this.inputValue;
                let word = this.formData.styleNames;
                if (this.formData.styleNames.includes(inputValue)) {
                    this.$message.error('请不要重复添加');
                    return;
                }
                if (inputValue) {
                    word.push(inputValue);
                }
                this.inputVisible = false;
                this.inputValue = '';
            },
            confirmSize() {
                let inputValue = this.inputValue2;
                let word = this.formData.sizeNames;
                if (this.formData.sizeNames.includes(inputValue)) {
                    this.$message.error('请不要重复添加');
                    return;
                }
                if (inputValue) {
                    word.push(inputValue);
                }
                this.inputVisible2 = false;
                this.inputValue2 = '';
            },
            delStyle(tag) {
                let word = this.formData.styleNames;
                word.splice(word.indexOf(tag), 1);
            },
            delSize(tag) {
                let word = this.formData.sizeNames;
                word.splice(word.indexOf(tag), 1);
                //同步删除 壁纸bind的尺寸
                let picDescs = this.formData.picDescs;
                if (picDescs) {
                    picDescs.forEach(o => {
                        if (o.name == tag) {
                            o.name = '请点击选择尺寸';
                        }
                    });
                }
            },
            addStyle() {
                this.inputVisible = true;
                this.$nextTick(_ => {
                    this.$refs.saveTagInput.$refs.input.focus();
                });
            },
            addSize() {
                this.inputVisible2 = true;
                this.$nextTick(_ => {
                    this.$refs.saveTagInput2.$refs.input.focus();
                });
            },
            coverSuccess(res, file) {
                this.formData.coverUrl = URL.createObjectURL(file.raw);
                this.formData.coverId = res.data.id;
            },
            proSuccess(res, file) {
                this.productImageUrl = res.data.fullUrl;
                this.productDilogData.content = res.data.url;
                this.productDilogData.fullUrl = res.data.fullUrl;
            },
            lunBoSuccess(response, file, fileList) {
                this.formData.banners.push(response.data.id);
            },
            stickSizeSuccess(response, file, fileList) {
                file.name = '请点击选择尺寸';
                let stickSize = {
                    imageId: response.data.id,
                    url: response.data.fullUrl,
                    sizeName: ""
                }
                file.stickSize = stickSize;
                this.formData.picDescs.push(file);
            },
            lunBoPrev(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            stickSizePrev(file) {//壁纸选择尺寸
                let _self = this;
                if (_self.formData.sizeNames.length == 0) {
                    _self.$message.error('请先添加尺寸');
                    return;
                }
                //定位当前图片
                this.stickSizeId = file.stickSize.imageId;
                console.log(file);
                //尺寸
                this.sizeName = '';
                let fileName = file.name;
                if (fileName != '') {
                    this.sizeName = fileName;
                }
                _self.stickVisible = true;
            },
            stickRemove(file, fileList) {
                let currentImageId = file.stickSize.imageId;
                this.formData.picDescs = this.formData.picDescs.filter(o => {
                    let curImageId = o.stickSize.imageId;
                    return curImageId != currentImageId;
                });
            },
            lunBoRemove(file, fileList) {
                this.formData.banners.pop(file);
            },
            sure(descType) {
                this.productVisible = true;
                let obj = {
                    type: descType,
                    content: '',
                    fullUrl: ''
                };
                this.productDilogData = obj;
                console.log(this.productDilogData);
                this.index = '';
            },
            edit(item, index) {
                this.productDilogData = item;
                this.index = index;
                if (this.productDilogData.type == 2) {
                    let fullUrl = this.productDilogData.fullUrl;
                    if (fullUrl) {
                        this.productImageUrl = fullUrl;
                    } else {
                        this.productImageUrl = this.productDilogData.content;
                    }
                }
                this.productVisible = true;
            },
            del(o) {
                this.formData.description.pop(o);
            },
            cancel() {
                this.index = '';
                this.productVisible = false;
                this.productDilogData = {
                    type: '',
                    content: '',
                    fullUrl: ''
                }
            },
            //商品介绍弹框提交按钮
            proSubmit() {
                if (this.productDilogData === '') {
                    this.$message.error('请输入内容后提交');
                }
                if (this.index === '') {
                    this.formData.description.push(this.productDilogData);
                } else {
                    this.formData.description[this.index] = this.productDilogData;
                }
                this.productVisible = false;
                this.index = '';
            },
            stickSizeSubmit() {
                let thisSizeName = this.sizeName;
                let thisStickId = this.stickSizeId;
                if (thisStickId === '') {
                    return;
                }
                //校验重复选择尺寸
                for (let o of this.formData.picDescs) {
                    let curSizeName = o.stickSize.sizeName;
                    let curImageId = o.stickSize.imageId;
                    if (curSizeName == thisSizeName) {
                        if (curImageId != thisStickId) {
                            this.$message.error('一张壁纸只需要对应一个尺寸');
                            return;
                        }
                    }
                }
                //赋值
                this.formData.picDescs.forEach(o => {
                    if (o.stickSize.imageId == this.stickSizeId) {
                        o.stickSize.sizeName = this.sizeName;
                        o.name = this.sizeName;
                    }
                });
                this.stickVisible = false;
            },
        },
        watch: {
            detail(val) { //监测详情变化
            },
            value(val) {
                this.visible = val;
            },
            visible(val) {
                this.$emit('input', val);
            }
        },
        mounted() {
            if (this.value) {
                this.visible = true;
            }
        }
    }
</script>



