<template>
    <section>
        <!--顶部工具条-->
        <el-col :span="24" class="toolbar">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.id" placeholder="请输入商品ID" icon="circle-close"
                              :on-icon-click="resetSearch"
                              @keyup.enter.native="fetchList"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input v-model="filters.name" placeholder="请输入商品名称" icon="circle-close"
                              :on-icon-click="resetSearch"
                              @keyup.enter.native="fetchList"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input v-model="filters.authorName" placeholder="请输入商家名称" icon="circle-close"
                              :on-icon-click="resetSearch"
                              @keyup.enter.native="fetchList"></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="fetchList()">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="showForm('-1')">新增</el-button>
                </el-form-item>
            </el-form>
        </el-col>

        <!--表格-->
        <el-table v-loading="tableLoading" class="table-expand" :data="tableList" stripe border
                  :max-height="tableHeight" style="width: 100%;">
            <el-table-column prop="id" label="id" width="100"></el-table-column>
            <el-table-column prop="coverUrl" label="商品封面">
                <template scope="scope">
                    <img v-if="scope.row.coverUrl != ''" style="width: 100%;vertical-align:middle;margin:10px auto;"
                         :src="scope.row.coverUrl" alt=""/>
                </template>
            </el-table-column>
            <el-table-column prop="name" label="商品名称"></el-table-column>
            <el-table-column label="商品单价" width="100">
                <template scope="scope">
                    ¥{{ scope.row.price }}
                </template>
            </el-table-column>
            <el-table-column label="商品类别" width="100">
                <template scope="scope">
                    {{ scope.row.type == 0 ? '商品' : '壁纸' }}
                </template>
            </el-table-column>
            <el-table-column prop="numBuy" label="购买总件数"></el-table-column>
            <el-table-column prop="authorName" label="商家"></el-table-column>
            <el-table-column prop="updateTime" label="更新时间"></el-table-column>

            <el-table-column label="操作" width="120">
                <template scope="scope">
                    <el-button-group class="mt-10">
                        <el-button size="small" @click="showForm(scope.row)">编辑</el-button>
                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="mt-10">
            <el-pagination style="float:right;" @current-change="handleCurrentChange"
                           :page-size="10" :current-page="page"
                           layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-col>

        <!--产品详情页-->
        <v-detail :data="data" :authors="authors" v-model="isShowForm" v-on:refresh="fetchList"></v-detail>

    </section>
</template>

<script type="es6">
    import util from '../../../api/util'
    import {httpGet} from '../../../api/api';
    import vDetail from './productDetail.vue'

    export default {
        components: {
            vDetail
        },
        data() {
            return {
                filters: {
                    name: '',
                    id: '',
                    authorName: ''
                },
                total: 0, //表格列表数据总数
                page: 1, //当前页，默认为第一页
                tableHeight: '100%',
                tableLoading: false, //表格的loading符号
                tableList: [], //表格数据
                isShowForm: false, //显示、隐藏编辑页
                data: {},
                authors: [],
            }
        },
        methods: {
            handleCurrentChange(val) { //翻页
                this.page = val;
                this.fetchList();
            },
            fetchList() { //获取列表
                let _self = this;
                _self.tableHeight = document.getElementById('container').clientHeight - 77 - 42 - 15;
                let para = {
                    offset: 0,
                    size: 10,
                    id: _self.filters.id,
                    name: _self.filters.name,
                    authorName: _self.filters.authorName,
                };

                para.offset = (_self.page - 1) * para.size;
                _self.tableLoading = true;
                httpGet('productList', para, _self, function (res) {
                    _self.tableLoading = false;
                    try {
                        let {error, status, data} = res;
                        _self.total = data.totalElements;
                        _self.tableList = data.content;
                    } catch (error) {
                        util.jsErrNotify(error);
                    }
                })
            },
            resetSearch() {
                this.filters = {
                    name: '',
                    id: '',
                    authorName: ''
                };
                this.fetchList();
            },
            showForm(row) { //显示表单
                this.isShowForm = true;
                this.data = row;
            },
            getAuthors() {
                let _self = this;
                let para = {
                };
                httpGet('authorList', para, _self, function (res) {
                    try {
                        let {error, status, data} = res;
                        if (data) {
                            _self.authors = data;
                        }
                    } catch (error) {
                        util.jsErrNotify(error);
                    }
                })
            }
        },
        mounted() {
            this.fetchList();
            this.getAuthors();
        }
    }
</script>

<style>
    /*
        表格展开部分样式
    */
    .table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 100%;
    }

</style>



